import { Routes } from '@angular/router';

export const routes: Routes = [
  { 
    path: 'lista-precios',
    loadComponent: () => import('./features/lista-precios/components/lista-precios.component')
      .then(m => m.ListaPreciosComponent),
    title: 'Lista de Precios'
  },
  { 
    path: 'lista-precios/cargar-excel', 
    loadComponent: () => import('./features/lista-precios/components/excel-uploade/excel-uploade.component')
      .then(m => m.ExcelUploadeComponent),
    title: 'Cargar Excel'
  },
  { 
    path: 'lista-precios/historico', 
    loadComponent: () => import('./features/lista-precios/components/historico-precios/historico-precios.component')
      .then(m => m.HistoricoPreciosComponent),
    title: 'Histórico de Precios'
  },
  { 
    path: 'lista-precios/registro-manual', 
    loadComponent: () => import('./features/lista-precios/components/precio-form/precio-form.component')
      .then(m => m.PrecioFormComponent),
    title: 'Registro Manual'
  },
  { 
    path: '',
    redirectTo: 'lista-precios',
    pathMatch: 'full'
  }
];